.v-controlBar {
  position: relative;
  width: 100%;
  height: 48px;
  border-bottom: 1px solid #eee;
  border-radius: 7px;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
  background-color: #f8f8f8;
}
.v-controlBar .apple {
  position: absolute;
  top: 24px;
  left: 3em;
  margin-top: -0.5em;
  width: 1em;
  height: 1em;
  border-radius: 50%;
  background-color: #fc615d;
  cursor: pointer;
}
.v-controlBar .apple::before,
.v-controlBar .apple::after {
  position: absolute;
  top: 0;
  width: 1em;
  height: 1em;
  border-radius: 50%;
  content: "";
  cursor: pointer;
}
.v-controlBar .apple::before {
  left: -1.5em;
  background-color: #fdbc40;
}
.v-controlBar .apple::after {
  right: -1.5em;
  background-color: #34c84a;
}
.v-controlBar .title {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  width: 100%;
  height: 100%;
}
.v-controlBar .title select {
  outline: none;
  border: none;
}
